$('.info__slider-wrap').slick({
    dots: true,
    infinite: true,
    speed: 500,
    fade: true,
    cssEase: 'linear',
    prevArrow: $('.info__prev'),
    nextArrow: $('.info__next'),
});

$('.feedbacks__slider-wrap').slick({
    infinite: true,
    slidesToShow: 3,
    slidesToScroll: 1,
    dots: true,
    prevArrow: $('.feedbacks__prev'),
    nextArrow: $('.feedbacks__next'),
    responsive: [
        {
            breakpoint: 1024,
            settings: {
                slidesToShow: 2,
            }
        },
        {
            breakpoint: 500,
            settings: {
                slidesToShow: 1,
            }
        },
    ]
});


$(document).ready(function() {

    //Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header__right').toggleClass('header__right--active')
        $('body').toggleClass('fixed-body')
    })

    // Tabs
    $(".price-card__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".price-card__tab").removeClass("price-card__tab--active")
        $(this).addClass("price-card__tab--active")

        $(this).parents(".price-card").find(".price").removeClass("price--active")
        $(this).parents(".price-card").find(`.price[data-price-path="${path}"]`).addClass("price--active")
    })

    $(".tab-checkbox").click(function () {
        let path = $(this).attr("data-tab-path2")
        $(this).siblings(".tab-checkbox").removeClass("tab-checkbox--active")
        $(this).addClass("tab-checkbox--active")

        $(this).parents(".price-card").find(".checkbox-content").removeClass("checkbox-content--active")
        $(this).parents(".price-card").find(`.checkbox-content[data-content-path="${path}"]`).addClass("checkbox-content--active")
    })

    //All
    $('.feedback-card__btn').click(function () {
        $(this).toggleClass('feedback-card__btn--active')
        $(this).parents('.feedback-card').find('.feedback-card__text').toggleClass('feedback-card__text--active')
    })

    // Animation
    const animItems = document.querySelectorAll('._anim-items')

    if (animItems.length > 0) {
        window.addEventListener('scroll', animOnScroll)
        function animOnScroll(params) {
            for (let index = 0; index < animItems.length; index++) {
                const animItem = animItems[index];
                const animItemHeight = animItem.offsetHeight;
                const animItemOffset = offset(animItem).top;
                const animStart = 4;

                let animItemPoint = window.innerHeight - animItemHeight / animStart;

                if (animItemHeight > window.innerHeight) {
                    animItemPoint = window.innerHeight - window.innerHeight / animStart;
                }

                if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                    animItem.classList.add('active');
                } else {
                    if (!animItem.classList.contains('anim-no-hide')) {
                        animItem.classList.remove('active')
                    }
                }
            }
        }
        function offset(el) {
            const rect = el.getBoundingClientRect(),
                scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
                scrollTop = window.pageYOffset || document.documentElement.scrollTop;
            return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
        }

        setTimeout(() => {
            animOnScroll();
        }, 300)
    }

})